////METODOS DE UTILIDAD PARA GRABAR TABLITA DE LA PREGUNTA 4471/////
get productosSinRotulado(): FormArray {
  return this.ficha4Form.get('productosSinRotulado') as FormArray;
}

crearFilaProducto(): FormGroup {
  return this.fb.group({
    tipo: [''],
    cantidad: [0]
  });
}

estadoFilasGuardadas: boolean[] = [];

