agregarFilaProducto(): void {
  const nuevoProducto = this.fb.group({
    id: [null],  //  ahora s
    tipo: [''],
    cantidad: [0]
  });

  this.productosSinRotulado.push(nuevoProducto);
}

guardarProductoIndividual(i: number): void {
  const fila = this.productosSinRotulado.at(i);
  if (!fila) return;

  const id = fila.get('id')?.value;
  const tipo = fila.get('tipo')?.value;
  const cantidad = fila.get('cantidad')?.value;
  const idFicha = this.ficha4Form.get('idFicha')?.value;

  if (!tipo || tipo.trim() === '' || !cantidad || cantidad <= 0) {
    Swal.fire({ icon: 'warning', title: 'Datos incompletos', text: 'Debe ingresar tipo y cantidad vlida' });
    return;
  }

  const data = { id, tipo, cantidad, idFicha };

  this.http.post<any>('/api/ficha/guardarProductoSinRotuladoIndividual', data).subscribe({
    next: (resp) => {
      //  Guarda el ID retornado si era nuevo
      if (!id && resp?.id) {
        fila.get('id')?.setValue(resp.id);
      }

      this.estadoFilasGuardadas[i] = true;
      Swal.fire({ icon: 'success', title: 'Guardado', timer: 1200, showConfirmButton: false });
    },
    error: () => {
      Swal.fire({ icon: 'error', title: 'Error', text: 'No se pudo guardar el producto.' });
    }
  });
}


private formatearFechasComoStringISO(data: any): any {
  const nuevo = { ...data };

  const camposFecha = ['p6Fch', 'p7Fch', 'p9Fch'];

  camposFecha.forEach(campo => {
    const valor = nuevo[campo];
    if (valor instanceof Date) {
      nuevo[campo] = valor.toISOString().split('T')[0]; // 'yyyy-MM-dd'
    }
  });

  return nuevo;
}


cargarProductosSinRotulado(idFicha: number): void {
  this.http.get<any[]>(`/api/ficha/obtenerProductosSinRotulado/${idFicha}`).subscribe({
    next: (productos) => {
      const productosFormArray = this.ficha4Form.get('productosSinRotulado') as FormArray;
      productosFormArray.clear();

      productos.forEach(prod => {
        productosFormArray.push(this.fb.group({
          id: [prod.id],
          tipo: [prod.tipo],
          cantidad: [prod.cantidad]
        }));
      });

      this.estadoFilasGuardadas = productos.map(() => true);
    }
  });
}


eliminarFilaProducto(index: number): void {
  const fila = this.productosSinRotulado.at(index);
  const id = fila?.get('id')?.value;

  // Si no tiene ID, solo lo eliminamos del array
  if (!id) {
    this.productosSinRotulado.removeAt(index);
    this.estadoFilasGuardadas.splice(index, 1); // sincroniza el estado visual
    return;
  }

  // Confirmar eliminacin
  Swal.fire({
    icon: 'question',
    title: 'Eliminar producto?',
    text: 'Esta accin no se puede deshacer.',
    showCancelButton: true,
    confirmButtonText: 'S, eliminar',
    cancelButtonText: 'Cancelar'
  }).then(result => {
    if (result.isConfirmed) {
      this.http.delete(`/api/ficha/eliminarProductoSinRotulado/${id}`).subscribe({
        next: () => {
          this.productosSinRotulado.removeAt(index);
          this.estadoFilasGuardadas.splice(index, 1);

          Swal.fire({
            icon: 'success',
            title: 'Producto eliminado',
            timer: 1200,
            showConfirmButton: false
          });
        },
        error: (err) => {
          console.error('Error al eliminar producto:', err);
          Swal.fire({
            icon: 'error',
            title: 'Error al eliminar',
            text: 'No se pudo eliminar el producto.'
          });
        }
      });
    }
  });
}

