private limpiarDetalleSiCheckboxDesmarcado(checkboxControlName: string, detalleControlName: string): void {
  this.ficha4Form.get(checkboxControlName)?.valueChanges.subscribe(valor => {
    const detalle = this.ficha4Form.get(detalleControlName);
    if (!valor) {
      detalle?.reset();
      detalle?.disable();
    } else {
      detalle?.enable();
    }
    detalle?.updateValueAndValidity();
  });
}





private controlarDetallePorCheckbox(
  nombreCheckbox: string,
  nombreDetalle: string
): void {
  const checkbox = this.ficha4Form.get(nombreCheckbox);
  const detalle = this.ficha4Form.get(nombreDetalle);

  if (!checkbox || !detalle) return;

  checkbox.valueChanges.subscribe(valor => {
    if (valor) {
      detalle.enable();
      detalle.setValidators([Validators.required]);
    } else {
      detalle.reset();
      detalle.disable();
      detalle.clearValidators();
      detalle.setErrors(null);
    }
    detalle.updateValueAndValidity();
  });

  //  Validacin inicial si viene precargado
  if (checkbox.value) {
    detalle.enable();
    detalle.setValidators([Validators.required]);
  } else {
    detalle.reset();
    detalle.disable();
    detalle.clearValidators();
    detalle.setErrors(null);
  }

  detalle.updateValueAndValidity();
}


 onOtroCheckboxChange(nombreCheckbox: string, nombreDetalle: string): void {
  const marcado = this.ficha4Form.get(nombreCheckbox)?.value;
  const detalle = this.ficha4Form.get(nombreDetalle);

  if (marcado) {
    detalle?.enable();
    detalle?.setValidators([Validators.required, Validators.maxLength(1000)]);
  } else {
    detalle?.reset();
    detalle?.disable();
    detalle?.clearValidators();
    detalle?.setErrors(null);
  }

  detalle?.updateValueAndValidity();
}


private transformarValoresCheckbox(formData: any): any {
  const transformValue = (key: string, value: any): any => {
    // Si es booleano y no es un campo *_region, transforma a 'S' / ''
    if (typeof value === 'boolean' && !key.endsWith('_region')) {
      return value ? 'S' : '';
    } else if (value === null || value === undefined) {
      return '';
    } else if (Array.isArray(value)) {
      return value.map(v => transformValue(key, v));
    } else if (typeof value === 'object' && value !== null) {
      return transformObject(value);
    }
    return value;
  };

  const transformObject = (obj: any): any => {
    const result: any = {};
    for (const key in obj) {
      if (obj.hasOwnProperty(key)) {
        result[key] = transformValue(key, obj[key]);
      }
    }
    return result;
  };

  return transformObject(formData);
}


  validarLongitudMaxima(controlName: string, maxLength: number): void {
  const control = this.ficha4Form.get(controlName);
  if (control) {
    const valor = control.value;
    if (valor && valor.length > maxLength) {
      control.setValue(valor.slice(0, maxLength));
    }
  }
}



private controlarCampoDetalle(radioControlName: string, detalleControlName: string, habilitarValor: string): void {
  this.ficha4Form.get(radioControlName)?.valueChanges.subscribe(value => {
    const detalleControl = this.ficha4Form.get(detalleControlName);
    if (value !== habilitarValor) {
      // Limpiar y deshabilitar el campo de detalle si no coincide con el valor habilitador
      detalleControl?.reset();
      detalleControl?.disable();
    } else {
      // Habilitar el campo de detalle si coincide con el valor habilitador
      detalleControl?.enable();
    }
  });
}



private validarGrupoCheckboxes(checkboxes: string[]): void {
  const validarGrupo = () => {
    const algunoSeleccionado = checkboxes.some(nombre => this.ficha4Form.get(nombre)?.value);
    const error = algunoSeleccionado ? null : { required: true };
    checkboxes.forEach(nombre => {
      this.ficha4Form.get(nombre)?.setErrors(error);
    });
  };

  // Escuchar cambios en los checkboxes
  checkboxes.forEach(nombre => {
    this.ficha4Form.get(nombre)?.valueChanges.subscribe(() => validarGrupo());
  });

  // Ejecutar la validacin inicial
  validarGrupo();
}




