private configurarValidacionP410(): void {
    // Asegrate de que p4_10 sea obligatorio
    this.ficha4Form.get('p4_10')?.setValidators([Validators.required]);

    // Gestiona la validacin dinmica de p4_10Detalle
    this.ficha4Form.get('p4_10')?.valueChanges.subscribe((valor) => {
      const detalleControl = this.ficha4Form.get('p4_10Detalle');
      if (valor === 'C') {
        detalleControl?.setValidators([Validators.required]);
      } else {
        detalleControl?.clearValidators();
        detalleControl?.reset(); // Limpia el valor si no es necesario
      }
      detalleControl?.updateValueAndValidity();
    });
  }



  
  private controlarGrupoCheckboxesPorRadio(
  controlRadioName: string,
  checkboxNames: string[],
  valorDeshabilitador: string,
  detalleCheckboxName: string
): void {
  const controlRadio = this.ficha4Form.get(controlRadioName);
  if (!controlRadio) return;

  controlRadio.valueChanges.subscribe(valor => {
    const detalleControl = this.ficha4Form.get(detalleCheckboxName);

    if (valor === valorDeshabilitador) {
      //  Deshabilitar y limpiar TODOS los checkboxes y campos relacionados
      checkboxNames.forEach(nombre => {
        const control = this.ficha4Form.get(nombre);
        control?.reset();
        control?.disable();
        control?.clearValidators();
        control?.setErrors(null);
        control?.updateValueAndValidity();
      });

      if (detalleControl) {
        detalleControl.reset();
        detalleControl.disable();
        detalleControl.clearValidators();
        detalleControl.setErrors(null);
        detalleControl.updateValueAndValidity();
      }

    } else {
      //  Habilitar todos los checkboxes y campos relacionados
      checkboxNames.forEach(nombre => {
        const checkbox = this.ficha4Form.get(nombre);
        checkbox?.enable();
        checkbox?.updateValueAndValidity();
      });

      if (detalleControl) {
        detalleControl.enable();
        detalleControl.updateValueAndValidity();
      }

      // Validar al menos un checkbox seleccionado
      const validarGrupo = () => {
        const algunoSeleccionado = checkboxNames.some(nombre => this.ficha4Form.get(nombre)?.value);
        const error = algunoSeleccionado ? null : { required: true };
        checkboxNames.forEach(nombre => {
          const checkbox = this.ficha4Form.get(nombre);
          if (checkbox) checkbox.setErrors(error);
        });
      };

      checkboxNames.forEach(nombre => {
        this.ficha4Form.get(nombre)?.valueChanges.subscribe(() => validarGrupo());
      });

      validarGrupo();
    }
  });

  // Validacin inicial por si el formulario viene precargado
  const valorActual = controlRadio.value;
  const detalleControl = this.ficha4Form.get(detalleCheckboxName);

  if (valorActual === valorDeshabilitador) {
    checkboxNames.forEach(nombre => {
      const control = this.ficha4Form.get(nombre);
      control?.reset();
      control?.disable();
      control?.clearValidators();
      control?.setErrors(null);
      control?.updateValueAndValidity();
    });

    if (detalleControl) {
      detalleControl.reset();
      detalleControl.disable();
      detalleControl.clearValidators();
      detalleControl.setErrors(null);
      detalleControl.updateValueAndValidity();
    }
  }
}





  private configurarValidacionP411(): void {
    // Asegrate de que p4_10 sea obligatorio
    this.ficha4Form.get('p4_11')?.setValidators([Validators.required]);

  
  }

verificarCamposInvalidos(): void {
  Object.keys(this.ficha4Form.controls).forEach(controlName => {
    const control = this.ficha4Form.get(controlName);
    if (control && control.invalid) {
      console.warn(` Campo invlido: ${controlName}`, control.errors);
    }
  });
}


  private controlarP412C(): void { 
  const detalleControl = this.ficha4Form.get('p4_12CDetalle');
  const value = this.ficha4Form.get('p4_12C')?.value;

  // Evaluacin inicial (al cargar el formulario, til en modo edicin)
  if (value) {
    detalleControl?.enable();
  } else {
    detalleControl?.reset();
    detalleControl?.disable();
  }

  detalleControl?.updateValueAndValidity();

  // Suscripcin para cambios futuros
  this.ficha4Form.get('p4_12C')?.valueChanges.subscribe((value) => {
    if (!value) {
      detalleControl?.reset();
      detalleControl?.disable();
    } else {
      detalleControl?.enable();
    }
    detalleControl?.updateValueAndValidity();
  });
}


private cargarDatosDesdeLocalStorage(): void {
  const s4Data = localStorage.getItem('pantbc_s4');
  if (s4Data) {
    const datos = JSON.parse(s4Data);
    this.ficha4Form.patchValue(datos);

    // Verificar estado inicial de p6 si ya viene marcado como "S"
    this.verificarEstadoP6(datos.p6);

    this.verificarEstadoP7(datos.p7);

  }
}

private controlarPreguntaP6(): void {
  this.ficha4Form.get('p6')?.valueChanges.subscribe(valor => {
    this.verificarEstadoP6(valor);
  });
}
private verificarEstadoP6(valor: string): void {
  const detalle = this.ficha4Form.get('p6Detalle');
  const fecha = this.ficha4Form.get('p6Fch');

  if (valor === 'S') {
    detalle?.setValidators([Validators.required]);
    fecha?.setValidators([Validators.required]);
    detalle?.enable();
    fecha?.enable();
  } else {
    detalle?.clearValidators();
    fecha?.clearValidators();
    detalle?.reset();
    fecha?.reset();
    detalle?.disable();
    fecha?.disable();
  }

  detalle?.updateValueAndValidity();
  fecha?.updateValueAndValidity();
}

