private configurarPreguntaP8(): void {
  const checkboxes = ['p8A', 'p8B', 'p8C', 'p8D', 'p8E'];
  const detalleControl = this.ficha4Form.get('p8EDetalle');

  const validarGrupo = () => {
    const algunoMarcado = checkboxes.some(nombre => this.ficha4Form.get(nombre)?.value);
    const error = algunoMarcado ? null : { required: true };

    checkboxes.forEach(nombre => {
      const control = this.ficha4Form.get(nombre);
      control?.setErrors(error);
    });

    // Habilitar detalle si se marca p8E, si no, limpiar y deshabilitar
    const p8E = this.ficha4Form.get('p8E')?.value;
    if (p8E) {
      detalleControl?.setValidators([Validators.required]);
      detalleControl?.enable();
    } else {
      detalleControl?.clearValidators();
      detalleControl?.reset();
      detalleControl?.disable();
    }
    detalleControl?.updateValueAndValidity();
  };

  // Escuchar cambios en los checkboxes
  checkboxes.forEach(nombre => {
    this.ficha4Form.get(nombre)?.valueChanges.subscribe(() => {
      validarGrupo();
    });
  });

  // Validacin inicial (por si se carga en modo edicin)
  validarGrupo();
}
///
private controlarPreguntaP9(): void {
  const fechaControl = this.ficha4Form.get('p9Fch');

  this.ficha4Form.get('p9')?.valueChanges.subscribe(valor => {
    if (valor === 'S') {
      fechaControl?.setValidators([Validators.required]);
      fechaControl?.enable();
    } else {
      fechaControl?.clearValidators();
      fechaControl?.reset();
      fechaControl?.disable();
    }
    fechaControl?.updateValueAndValidity();
  });

  // Validacin inicial por si ya viene un valor seleccionado (modo edicin)
  if (this.ficha4Form.get('p9')?.value === 'S') {
    fechaControl?.enable();
    fechaControl?.setValidators([Validators.required]);
  } else {
    fechaControl?.disable();
    fechaControl?.clearValidators();
    fechaControl?.reset();
  }
  fechaControl?.updateValueAndValidity();
}




private inicializarEstadoDetalles(): void {
  const campos = [
    { checkbox: 'p10C1F', detalle: 'p10C1FDetalle' },
    { checkbox: 'p10C1G', detalle: 'p10C1GDetalle' },
    { checkbox: 'p10C2E', detalle: 'p10C2EDetalle' },
    { checkbox: 'p10C2F', detalle: 'p10C2FDetalle' },
    { checkbox: 'p10C2G', detalle: 'p10C2GDetalle' },
  ];

  campos.forEach(({ checkbox, detalle }) => {
    const valorCheckbox = this.ficha4Form.get(checkbox)?.value;
    const valorDetalle = this.ficha4Form.get(detalle)?.value;

    if (valorCheckbox || valorDetalle) {
      this.ficha4Form.get(detalle)?.enable(); // Si ya hay info, habilita
    } else {
      this.ficha4Form.get(detalle)?.disable(); // Si est vaco, deshabilita
    }

    // Siempre agregar el listener
    this.controlarDetallePorCheckbox(checkbox, detalle);
  });
}


