@Output() onEstadoActualizado = new EventEmitter<string>(); // NUEVO


guardarSeccion4() {

  this.guardarConPost();
}


private guardarConPost(): void {
  let seccionData = this.ficha4Form.getRawValue();

// 1. Formatear fechas como 'yyyy-MM-dd'
seccionData = this.formatearFechasComoStringISO(seccionData);

// 2. Transformar booleanos a 'S' o ''
seccionData = this.transformarValoresCheckbox(seccionData);


  //  3. Convertir fechas a ISO (yyyy-MM-dd)
  ['p6Fch', 'p7Fch', 'p9Fch'].forEach(campo => {
    if (seccionData[campo] instanceof Date) {
      seccionData[campo] = seccionData[campo].toISOString().split('T')[0];
    }
  });


  console.log('Datos transformados:', JSON.stringify(seccionData, null, 2));
  console.log('p6Fch despus de formatear:', seccionData.p6Fch);
  console.log('Tipo de p6Fch:', typeof this.ficha4Form.get('p6Fch')?.value);
  console.log('Instancia de Date:', this.ficha4Form.get('p6Fch')?.value instanceof Date)

   seccionData.estado_s4 = 'C'
  // 4. Enviar al backend
  this.http.post<any>('/api/ficha/guardarFichaSeccion4', seccionData).subscribe({
    next: (respuesta) => {
      console.log('Mensaje:', respuesta.mensaje);
      console.log('Estado:', respuesta.estado);

      localStorage.setItem('pantbc_s4', JSON.stringify(seccionData));
      this.onGuardar.emit();
       // this.onEstadoActualizado.emit('I'); // Emitimos 'I'
        this.onEstadoActualizado.emit(seccionData.estado_s4); 

      Swal.fire({
        icon: 'success',
        title: respuesta.mensaje,
        text: `Estado: ${respuesta.estado}`,
        showConfirmButton: false,
        timer: 2500
      });
    },
    error: (err) => {
      console.error('Error al guardar seccin 4:', err);
      Swal.fire({
        icon: 'error',
        title: 'Error al guardar',
        text: 'No se pudo guardar la Seccin 4',
        confirmButtonText: 'OK'
      });
    }
  });
}


 
