private transformarValoresCheckbox(formData: any): any {
  // Funcin recursiva para procesar objetos anidados
  const transformValue = (value: any): any => {
    if (typeof value === 'boolean') {
      return value ? 'S' : '';
    } else if (value === null || value === undefined) {
      return '';
    } else if (Array.isArray(value)) {
      return value.map(transformValue);
    } else if (typeof value === 'object' && value !== null) {
      return transformObject(value);
    }
    return value;
  };

  // Funcin para procesar objetos
  const transformObject = (obj: any): any => {
    const result: any = {};
    for (const key in obj) {
      if (obj.hasOwnProperty(key)) {
        result[key] = transformValue(obj[key]);
      }
    }
    return result;
  };

  // Procesar el objeto principal
  return transformObject(formData);
}

private controlarCampoDetalle(radioControlName: string, detalleControlName: string, habilitarValor: string): void {
  this.ficha2Form.get(radioControlName)?.valueChanges.subscribe(value => {
    const detalleControl = this.ficha2Form.get(detalleControlName);
    if (value !== habilitarValor) {
      // Limpiar y deshabilitar el campo de detalle si no coincide con el valor habilitador
      detalleControl?.reset();
      detalleControl?.disable();
    } else {
      // Habilitar el campo de detalle si coincide con el valor habilitador
      detalleControl?.enable();
    }
  });
}
