private controlarPregunta21(): void {
  const valorP1 = this.ficha2Form.get('p1')?.value;
  const checkboxes = ['p1_1A', 'p1_1B', 'p1_1C', 'p1_1D', 'p1_1E'];

  // Quitar validaciones y limpiar errores previos
  checkboxes.forEach(nombre => {
    const ctrl = this.ficha2Form.get(nombre);
    ctrl?.clearValidators();
    ctrl?.setErrors(null);
    ctrl?.updateValueAndValidity({ emitEvent: false });
  });

  this.ficha2Form.get('p1_1EDetalle')?.reset();

  if (valorP1 === 'A' || valorP1 === 'B') {
    const actualizarErrores = () => {
      const algunoMarcado = checkboxes.some(n => this.ficha2Form.get(n)?.value === true);
      const error = algunoMarcado ? null : { required: true };
      checkboxes.forEach(nombre => {
        const ctrl = this.ficha2Form.get(nombre);
        ctrl?.setErrors(error);
      });
    };

    // Validar al inicio
    actualizarErrores();

    // Escuchar cambios de cada checkbox una sola vez
    checkboxes.forEach(nombre => {
      const ctrl = this.ficha2Form.get(nombre);
      ctrl?.valueChanges.subscribe(() => actualizarErrores());
    });
  } else {
    // Si NO es A ni B, limpiar valores
    checkboxes.forEach(nombre => {
      this.ficha2Form.get(nombre)?.setValue(false, { emitEvent: false });
    });
  }
}



permitirHasta9Numeros(event: KeyboardEvent, controlName: string): void {
  const input = event.target as HTMLInputElement;
  const valor = input.value;

  // Permitir teclas de control
  const teclasPermitidas = ['Backspace', 'Delete', 'ArrowLeft', 'ArrowRight', 'Tab'];
  if (teclasPermitidas.includes(event.key)) {
    return;
  }

  // Validar que el valor no exceda los 9 dgitos
  if (valor.length >= 9) {
    event.preventDefault();
  }
}

