private configurarActivacionCheckbox(checkboxName: string, campos: string[]): void {
  this.ficha2Form.get(checkboxName)?.valueChanges.subscribe((checked: boolean) => {
    campos.forEach(campo => {
      const control = this.ficha2Form.get(campo);
      if (control) {
        if (checked) {
          control.setValidators([Validators.required]);
          control.enable();
        } else {
          //control.clearValidators();
          control.reset();
          control.disable();
        }
        control.updateValueAndValidity();
      }
    });
  });
}



  private controlarGrupoCheckboxesPorRadio(
  controlRadioName: string,
  checkboxNames: string[],
  valorDeshabilitador: string,
  detalleCheckboxName: string
): void {
  const controlRadio = this.ficha2Form.get(controlRadioName);
  if (!controlRadio) return;

  controlRadio.valueChanges.subscribe(valor => {
    const detalleControl = this.ficha2Form.get(detalleCheckboxName);

    if (valor === valorDeshabilitador) {
      //  Deshabilitar y limpiar TODOS los checkboxes y campos relacionados
      checkboxNames.forEach(nombre => {
        const control = this.ficha2Form.get(nombre);
        control?.reset();
        control?.disable();
        control?.clearValidators();
        control?.setErrors(null);
        control?.updateValueAndValidity();
      });

      if (detalleControl) {
        detalleControl.reset();
        detalleControl.disable();
        detalleControl.clearValidators();
        detalleControl.setErrors(null);
        detalleControl.updateValueAndValidity();
      }

    } else {
      //  Habilitar todos los checkboxes y campos relacionados
      checkboxNames.forEach(nombre => {
        const checkbox = this.ficha2Form.get(nombre);
        checkbox?.enable();
        checkbox?.updateValueAndValidity();
      });

      if (detalleControl) {
        detalleControl.enable();
        detalleControl.updateValueAndValidity();
      }

      // Validar al menos un checkbox seleccionado
      const validarGrupo = () => {
        const algunoSeleccionado = checkboxNames.some(nombre => this.ficha2Form.get(nombre)?.value);
        const error = algunoSeleccionado ? null : { required: true };
        checkboxNames.forEach(nombre => {
          const checkbox = this.ficha2Form.get(nombre);
          if (checkbox) checkbox.setErrors(error);
        });
      };

      checkboxNames.forEach(nombre => {
        this.ficha2Form.get(nombre)?.valueChanges.subscribe(() => validarGrupo());
      });

      validarGrupo();
    }
  });

  // Validacin inicial por si el formulario viene precargado
  const valorActual = controlRadio.value;
  const detalleControl = this.ficha2Form.get(detalleCheckboxName);

  if (valorActual === valorDeshabilitador) {
    checkboxNames.forEach(nombre => {
      const control = this.ficha2Form.get(nombre);
      control?.reset();
      control?.disable();
      control?.clearValidators();
      control?.setErrors(null);
      control?.updateValueAndValidity();
    });

    if (detalleControl) {
      detalleControl.reset();
      detalleControl.disable();
      detalleControl.clearValidators();
      detalleControl.setErrors(null);
      detalleControl.updateValueAndValidity();
    }
  }
}



private habilitarCamposConValores(): void {
  const campos = [
    { checkbox: 'p7_1A', inputs: ['p7_1A24', 'p7_1A25'] },
    { checkbox: 'p7_1B', inputs: ['p7_1B24', 'p7_1B25'] },
    { checkbox: 'p7_1C', inputs: ['p7_1C24', 'p7_1C25'] },
    { checkbox: 'p7_1D', inputs: ['p7_1D24', 'p7_1D25'] },
    { checkbox: 'p7_1E', inputs: ['p7_1E24', 'p7_1E25', 'p7_1EDetalle'] },
    { checkbox: 'p7_1F', inputs: ['p7_1F24', 'p7_1F25', 'p7_1FDetalle'] },
    { checkbox: 'p7_1G', inputs: ['p7_1G24', 'p7_1G25', 'p7_1GDetalle'] },
  ];

  campos.forEach(({ checkbox, inputs }) => {
    const checkboxControl = this.ficha2Form.get(checkbox);
    if (checkboxControl?.value) {
      inputs.forEach(inputName => {
        const inputControl = this.ficha2Form.get(inputName);
        if (inputControl && inputControl.value !== null) {
          inputControl.enable();
        }
      });
    }
  });

  // Forzar la deteccin de cambios
  this.cdr.detectChanges();
}

 limpiarCamposP7_1(): void {
    const controls = [
      'p7_1A', 'p7_1A24', 'p7_1A25',
      'p7_1B', 'p7_1B24', 'p7_1B25',
      'p7_1C', 'p7_1C24', 'p7_1C25',
      'p7_1D', 'p7_1D24', 'p7_1D25',
      'p7_1E', 'p7_1EDetalle', 'p7_1E24', 'p7_1E25',
      'p7_1F', 'p7_1FDetalle', 'p7_1F24', 'p7_1F25',
      'p7_1G', 'p7_1GDetalle', 'p7_1G24', 'p7_1G25'
    ];

    controls.forEach(controlName => {
      const control = this.ficha2Form.get(controlName);
      if (control) {
        if (controlName.includes('Detalle')) {
          control.setValue('');
        } else if (controlName.includes('p7_1')) {
          if (controlName.length === 5) { // Es un checkbox (p7_1A, p7_1B, etc.)
            control.setValue(false);
          } else { // Es un campo numrico
            control.setValue('');
          }
        }
        control.markAsPristine();
        control.markAsUntouched();
        control.updateValueAndValidity();
      }
    });
  }

