  @Output() onGuardar = new EventEmitter<void>();
@Output() onEstadoActualizado = new EventEmitter<string>(); // NUEVO

guardarSeccion2() {
  if (this.ficha2Form.invalid) {
    console.log('Formulario Incompleto', this.ficha2Form.errors);
    return;
  }

  const formValue = this.ficha2Form.value;
  console.log('Valores originales:', JSON.stringify(formValue, null, 2));

  const seccionData = this.transformarValoresCheckbox(this.ficha2Form.value);
  console.log('Datos transformados:', JSON.stringify(seccionData, null, 2));


  seccionData.estado_s2 = 'C' ;
  this.http.post<any>('/api/ficha/guardarFichaSeccion2', seccionData).subscribe({
    next: (respuesta) => {
      console.log('Mensaje:', respuesta.mensaje);
      console.log('Estado:', respuesta.estado);

      localStorage.setItem('pantbc_s2', JSON.stringify(seccionData));
      this.onGuardar.emit();
    this.onEstadoActualizado.emit(seccionData.estado_s2); //  EMITE estado_s2

      //  Mensaje con SweetAlert
      Swal.fire({
        icon: 'success',
        title: respuesta.mensaje,
        text: `Estado: ${respuesta.estado}`,
        showConfirmButton: false,
        timer: 2500
      });
    },
    error: (err) => {
      console.error('Error al guardar seccin 2:', err);

      //  SweetAlert para error
      Swal.fire({
        icon: 'error',
        title: 'Error al guardar',
        text: 'No se pudo guardar la Seccin 2',
        confirmButtonText: 'OK'
      });
    }
  });
}
  
  guardarSeccion2Incompleta() {
  const seccionData = this.transformarValoresCheckbox(this.ficha2Form.value);
  seccionData.estado_s2 = 'I'; //  Siempre guardar como INCOMPLETO

  this.http.post<any>('/api/ficha/guardarFichaSeccion2', seccionData).subscribe({
    next: (respuesta) => {
      console.log('Mensaje:', respuesta.mensaje);
      console.log('Estado:', respuesta.estado);

      localStorage.setItem('pantbc_s2', JSON.stringify(seccionData));
      this.onGuardar.emit();
      this.onEstadoActualizado.emit('I'); // Emitimos 'I'

      Swal.fire({
        icon: 'info',
        title: 'Guardado Parcialmente',
        text: 'La Seccin 2 fue guardada de forma PARCIAL',
        timer: 2500,
        showConfirmButton: false
      });
    },
    error: (err) => {
      console.error('Error al guardar seccin 2 como incompleta:', err);
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: 'No se pudo guardar como incompleto',
      });
    }
  });
}

