  onOtroCheckboxChange(checkboxControlName: string, detalleControlName: string): void {
    const checkboxControl = this.ficha6Form.get(checkboxControlName);
    const detalleControl = this.ficha6Form.get(detalleControlName);
    if (checkboxControl?.value === false) {
      detalleControl?.reset();
      detalleControl?.disable(); 
    } else {
      detalleControl?.enable(); 
    }
  }

private transformarValoresCheckbox(formData: any): any {
  const transformValue = (key: string, value: any): any => {
    // Si es booleano y no es un campo *_region, transforma a 'S' / ''
    if (typeof value === 'boolean' && !key.endsWith('_region')) {
      return value ? 'S' : '';
    } else if (value === null || value === undefined) {
      return '';
    } else if (Array.isArray(value)) {
      return value.map(v => transformValue(key, v));
    } else if (typeof value === 'object' && value !== null) {
      return transformObject(value);
    }
    return value;
  };

  const transformObject = (obj: any): any => {
    const result: any = {};
    for (const key in obj) {
      if (obj.hasOwnProperty(key)) {
        result[key] = transformValue(key, obj[key]);
      }
    }
    return result;
  };

  return transformObject(formData);
}


