private controlarPregunta611(): void {
  const valorP5 = this.ficha6Form.get('p6_1')?.value;

  const checkboxes = ['p6_1_1_a', 'p6_1_1_b', 'p6_1_1_c', 'p6_1_1_d', 'p6_1_1_e'];
  const detalle = this.ficha6Form.get('p6_1_1_e_detalle');

  // Siempre limpiar validaciones anteriores
  checkboxes.forEach(nombre => {
    const control = this.ficha6Form.get(nombre);
    control?.clearValidators();
    control?.setErrors(null);
    control?.updateValueAndValidity({ emitEvent: false });
  });

  detalle?.setErrors(null);
  detalle?.updateValueAndValidity({ emitEvent: false });

  if (valorP5 === 'N') {
    // Si marc "NO", limpia todo y deja sin validadores
    checkboxes.forEach(nombre => {
      this.ficha6Form.get(nombre)?.setValue(false, { emitEvent: false });
    });
    detalle?.reset();
    return;
  }

  if (valorP5 === 'S') {
    // Validador personalizado para requerir al menos un checkbox
    const validarGrupo = () => {
      const algunoMarcado = checkboxes.some(n => this.ficha6Form.get(n)?.value === true);
      const error = algunoMarcado ? null : { required: true };
      checkboxes.forEach(n => this.ficha6Form.get(n)?.setErrors(error));
    };

    // Validar al inicio
    validarGrupo();

    // Suscribirse una sola vez a cambios
    checkboxes.forEach(nombre => {
      this.ficha6Form.get(nombre)?.valueChanges.subscribe(() => validarGrupo());
    });
  }
}






