private configurarPregunta612(): void {
  const pregunta612 = this.ficha6Form.get('p6_1');
  const ene = this.ficha6Form.get('p6_1_2_24');
  const feb = this.ficha6Form.get('p6_1_2_25');


  if (!pregunta612 || !ene || !feb ) return;

  // Suscripcin reactiva a cambios 
  pregunta612.valueChanges.subscribe((valor: string) => {
    const controles = [ene, feb ];

    if (valor === 'S') {
      controles.forEach(control => {
        control.setValidators([Validators.required]);
        control.updateValueAndValidity();
      });
    } else {
      controles.forEach(control => {
        control.reset();
        control.clearValidators();
        control.setErrors(null);
        control.updateValueAndValidity();
      });
    }
  });

  // Evaluacin inicial (por si ya vino seteado al editar)
  if (pregunta612.value === 'S') {
    [ene, feb].forEach(c => {
      c.setValidators([Validators.required]);
      c.updateValueAndValidity();
    });
  }
}


