  onOtroCheckboxChange(nombreCheckbox: string, nombreDetalle: string): void {
  const marcado = this.ficha7Form.get(nombreCheckbox)?.value;
  const detalle = this.ficha7Form.get(nombreDetalle);

  if (marcado) {
    detalle?.enable();
    detalle?.setValidators([Validators.required, Validators.maxLength(1000)]);
  } else {
    detalle?.reset();
    detalle?.disable();
    detalle?.clearValidators();
    detalle?.setErrors(null);
  }

  detalle?.updateValueAndValidity();
}

    validarLongitudMaxima(controlName: string, maxLength: number): void {
    const control = this.ficha7Form.get(controlName);
    if (control) {
      const valor = control.value;
      if (valor && valor.length > maxLength) {
        control.setValue(valor.slice(0, maxLength));
      }
    }
  }
////REUTILIZABLES/////

  private controlarCampoDetalle(radioControlName: string, detalleControlName: string, habilitarValor: string): void {
    const radioControl = this.ficha7Form.get(radioControlName);
    const detalleControl = this.ficha7Form.get(detalleControlName);

    if (!radioControl || !detalleControl) return;

    radioControl.valueChanges.subscribe(value => {
      if (value === habilitarValor) {
        detalleControl.setValidators([Validators.required]);
        detalleControl.enable();
      } else {
        detalleControl.clearValidators();
        detalleControl.reset();
        detalleControl.disable();
      }
      detalleControl.updateValueAndValidity();
    });

    //  Evala el valor inicial si ya viene cargado
    if (radioControl.value === habilitarValor) {
      detalleControl.setValidators([Validators.required]);
      detalleControl.enable();
    } else {
      detalleControl.clearValidators();
      detalleControl.disable();
    }
    detalleControl.updateValueAndValidity();
  }


  private controlarGrupoCheckboxesPorRadio(
  controlRadioName: string,
  checkboxNames: string[],
  valorDeshabilitador: string,
  detalleCheckboxName: string
): void {
  const controlRadio = this.ficha7Form.get(controlRadioName);
  if (!controlRadio) return;

  controlRadio.valueChanges.subscribe(valor => {
    const detalleControl = this.ficha7Form.get(detalleCheckboxName);

    if (valor === valorDeshabilitador) {
      //  Deshabilitar y limpiar TODOS los checkboxes y campos relacionados
      checkboxNames.forEach(nombre => {
        const control = this.ficha7Form.get(nombre);
        control?.reset();
        control?.disable();
        control?.clearValidators();
        control?.setErrors(null);
        control?.updateValueAndValidity();
      });

      if (detalleControl) {
        detalleControl.reset();
        detalleControl.disable();
        detalleControl.clearValidators();
        detalleControl.setErrors(null);
        detalleControl.updateValueAndValidity();
      }

    } else {
      //  Habilitar todos los checkboxes y campos relacionados
      checkboxNames.forEach(nombre => {
        const checkbox = this.ficha7Form.get(nombre);
        checkbox?.enable();
        checkbox?.updateValueAndValidity();
      });

      if (detalleControl) {
        detalleControl.enable();
        detalleControl.updateValueAndValidity();
      }

      // Validar al menos un checkbox seleccionado
      const validarGrupo = () => {
        const algunoSeleccionado = checkboxNames.some(nombre => this.ficha7Form.get(nombre)?.value);
        const error = algunoSeleccionado ? null : { required: true };
        checkboxNames.forEach(nombre => {
          const checkbox = this.ficha7Form.get(nombre);
          if (checkbox) checkbox.setErrors(error);
        });
      };

      checkboxNames.forEach(nombre => {
        this.ficha7Form.get(nombre)?.valueChanges.subscribe(() => validarGrupo());
      });

      validarGrupo();
    }
  });

  // Validacin inicial por si el formulario viene precargado
  const valorActual = controlRadio.value;
  const detalleControl = this.ficha7Form.get(detalleCheckboxName);

  if (valorActual === valorDeshabilitador) {
    checkboxNames.forEach(nombre => {
      const control = this.ficha7Form.get(nombre);
      control?.reset();
      control?.disable();
      control?.clearValidators();
      control?.setErrors(null);
      control?.updateValueAndValidity();
    });

    if (detalleControl) {
      detalleControl.reset();
      detalleControl.disable();
      detalleControl.clearValidators();
      detalleControl.setErrors(null);
      detalleControl.updateValueAndValidity();
    }
  }
}







private controlarGrupoCheckboxesPorRadiosMultiples(
  controlRadioName: string,
  checkboxNames: string[],
  valoresHabilitadores: string[],
  detalleCheckboxName: string
): void {
  const controlRadio = this.ficha7Form.get(controlRadioName);
  if (!controlRadio) return;

  const detalleControl = this.ficha7Form.get(detalleCheckboxName);

  const validarGrupo = () => {
    const algunoSeleccionado = checkboxNames.some(nombre => this.ficha7Form.get(nombre)?.value);
    const error = algunoSeleccionado ? null : { required: true };
    checkboxNames.forEach(nombre => {
      const checkbox = this.ficha7Form.get(nombre);
      if (checkbox) checkbox.setErrors(error);
    });
  };

  const limpiarYDeshabilitarGrupo = () => {
    checkboxNames.forEach(nombre => {
      const control = this.ficha7Form.get(nombre);
      control?.reset();
      control?.disable();
      control?.clearValidators();
      control?.setErrors(null);
      control?.updateValueAndValidity();
    });

    if (detalleControl) {
      detalleControl.reset();
      detalleControl.disable();
      detalleControl.clearValidators();
      detalleControl.setErrors(null);
      detalleControl.updateValueAndValidity();
    }
  };

  const habilitarGrupoYValidar = () => {
    checkboxNames.forEach(nombre => {
      const checkbox = this.ficha7Form.get(nombre);
      checkbox?.enable();
      checkbox?.updateValueAndValidity();

      this.ficha7Form.get(nombre)?.valueChanges.subscribe(() => validarGrupo());
    });

    if (detalleControl) {
      detalleControl.enable();
      detalleControl.updateValueAndValidity();
    }

    validarGrupo();
  };

  controlRadio.valueChanges.subscribe(valor => {
    setTimeout(() => {
      if (valoresHabilitadores.includes(valor)) {
        habilitarGrupoYValidar();
      } else {
        limpiarYDeshabilitarGrupo();
      }
    });
  });

  // Validacin inicial si el formulario viene precargado
  setTimeout(() => {
    if (valoresHabilitadores.includes(controlRadio.value)) {
      habilitarGrupoYValidar();
    } else {
      limpiarYDeshabilitarGrupo();
    }
  });
}



private configurarValidacionCondicional(
  controlPrincipal: string,
  camposDependientes: string[],
  valorHabilitador: string
): void {
  const control = this.ficha7Form.get(controlPrincipal);

  control?.valueChanges.subscribe(valor => {
    camposDependientes.forEach(nombre => {
      const campo = this.ficha7Form.get(nombre);
      if (valor === valorHabilitador) {
        campo?.setValidators([Validators.required]);
      } else {
        campo?.clearValidators();
        campo?.reset();
      }
      campo?.updateValueAndValidity();
    });
  });
  if (control?.value !== undefined) {
    camposDependientes.forEach(nombre => {
      const campo = this.ficha7Form.get(nombre);
      if (control.value === valorHabilitador) {
        campo?.setValidators([Validators.required]);
      } else {
        campo?.clearValidators();
        campo?.reset();
      }
      campo?.updateValueAndValidity();
    });
  }
}

