private activarDesactivarInputsPorCheckbox(): void {
  const controles = [
    { check: 'p7_1_1_a', campos: ['p7_1_1_a_24', 'p7_1_1_a_25'] },
    { check: 'p7_1_1_b', campos: ['p7_1_1_b_24', 'p7_1_1_b_25'] },
    { check: 'p7_1_1_c', campos: ['p7_1_1_c_24', 'p7_1_1_c_25'] },
    { check: 'p7_1_1_d', campos: ['p7_1_1_d_24', 'p7_1_1_d_25', 'p7_1_1_d_detalle'] },
    { check: 'p7_1_1_e', campos: ['p7_1_1_e_24', 'p7_1_1_e_25', 'p7_1_1_e_detalle'] }
  ];

  controles.forEach(({ check, campos }) => {
    this.ficha7Form.get(check)?.valueChanges.subscribe((valor: boolean) => {
      campos.forEach(campo => {
        const control = this.ficha7Form.get(campo);
        if (valor) {
          control?.enable();
        } else {
          control?.disable();
          control?.reset(); // opcional: limpia el campo si el checkbox se desmarca
        }
      });
    });

    // Estado inicial al cargar la vista
    if (!this.ficha7Form.get(check)?.value) {
      campos.forEach(campo => this.ficha7Form.get(campo)?.disable());
    }
  });
}




private configurarPregunta752(): void {
  const pregunta612 = this.ficha7Form.get('p7_5');
  const ene = this.ficha7Form.get('p7_5_2_24');
  const feb = this.ficha7Form.get('p7_5_2_25');


  if (!pregunta612 || !ene || !feb ) return;

  // Suscripcin reactiva a cambios 
  pregunta612.valueChanges.subscribe((valor: string) => {
    const controles = [ene, feb ];

    if (valor === 'S') {
      controles.forEach(control => {
        control.setValidators([Validators.required]);
        control.updateValueAndValidity();
      });
    } else {
      controles.forEach(control => {
        control.reset();
        control.clearValidators();
        control.setErrors(null);
        control.updateValueAndValidity();
      });
    }
  });

  // Evaluacin inicial (por si ya vino seteado al editar)
  if (pregunta612.value === 'S') {
    [ene, feb].forEach(c => {
      c.setValidators([Validators.required]);
      c.updateValueAndValidity();
    });
  }
}

verificarCamposInvalidos(): void {
  Object.keys(this.ficha7Form.controls).forEach(controlName => {
    const control = this.ficha7Form.get(controlName);
    if (control && control.invalid) {
      console.warn(` Campo invlido: ${controlName}`, control.errors);
    }
  });
}



