//////Funcion que se usa cuando una pregunta CHECKBOX DEPENDE DE RADIOS-- SOLO PARA CHECKBOX( la vu elve requerida y la limpia ) ///////////////////
private controlarGrupoCheckboxesPorRadio(
  controlRadioName: string,
  checkboxNames: string[],
  valorDeshabilitador: string,
  detalleCheckboxName: string
): void {
  const controlRadio = this.ficha3Form.get(controlRadioName);
  if (!controlRadio) return;

  controlRadio.valueChanges.subscribe(valor => {
    const detalleControl = this.ficha3Form.get(detalleCheckboxName);

    if (valor === valorDeshabilitador) {
      checkboxNames.forEach(nombre => {
        const control = this.ficha3Form.get(nombre);
        control?.reset();
        control?.clearValidators();
        control?.setErrors(null);
        control?.updateValueAndValidity();
      });
      if (detalleControl) {
        detalleControl.enable();           // 1. Habilitar para permitir setValue
        detalleControl.setValue('');       // 2. Limpiar valor visual
        detalleControl.reset();            // 3. Limpiar estado interno
       // detalleControl.disable();          // 4. Si debe quedar inhabilitado
        detalleControl.clearValidators();  // 5. Quitar validadores
        detalleControl.setErrors(null);    // 6. Quitar errores manuales
        detalleControl.updateValueAndValidity(); // 7. Actualizar el estado
      }

    } else {
      // Tambin limpiar el campo detalle si cambia a otro valor
      if (detalleControl) {
        detalleControl.enable();            // Necesario para limpiar bien
        detalleControl.setValue('');
        detalleControl.reset();
        detalleControl.clearValidators();
        detalleControl.updateValueAndValidity();
      }

      // Validar checkboxes como grupo requerido
      const validarGrupo = () => {
        const algunoSeleccionado = checkboxNames.some(nombre => this.ficha3Form.get(nombre)?.value);
        const error = algunoSeleccionado ? null : { required: true };
        checkboxNames.forEach(nombre => {
          const checkbox = this.ficha3Form.get(nombre);
          if (checkbox) checkbox.setErrors(error);
        });
      };

      checkboxNames.forEach(nombre => {
        this.ficha3Form.get(nombre)?.valueChanges.subscribe(() => validarGrupo());
      });

      validarGrupo(); // Validacin inicial si ya viene con valores
    }
  });

  // Validacin inicial si el formulario viene precargado
  if (controlRadio.value !== valorDeshabilitador) {
    const algunoSeleccionado = checkboxNames.some(nombre => this.ficha3Form.get(nombre)?.value);
    const error = algunoSeleccionado ? null : { required: true };
    checkboxNames.forEach(nombre => {
      const checkbox = this.ficha3Form.get(nombre);
      if (checkbox) checkbox.setErrors(error);
    });
  }
}




