private controlarPregunta311(): void {
  this.ficha3Form.get('p3_1')?.valueChanges.subscribe(valor => {
       const campos = ['p3_1_1A', 'p3_1_1B', 'p3_1_1C', 'p3_1_1D', 'p3_1_1DDetalle'];


    if (valor === 'S') {
      // Si la respuesta es "S", limpiar los campos relacionados con 1.4.2.1
      campos.forEach(nombre => {
        const control = this.ficha3Form.get(nombre);
        control?.reset();
        control?.clearValidators();
        control?.updateValueAndValidity();
      });
    } else if (valor === 'N') {
      // Si la respuesta es "No", validar que al menos un checkbox est seleccionado
      const checkboxes = campos.slice(0, 5); // Excluir el campo de detalle
      const validarGrupo = () => {
        const algunoSeleccionado = checkboxes.some(nombre => this.ficha3Form.get(nombre)?.value);
        const error = algunoSeleccionado ? null : { required: true };
        checkboxes.forEach(nombre => {
          this.ficha3Form.get(nombre)?.setErrors(error);
        });
      };

      // Escuchar cambios en los checkboxes
      checkboxes.forEach(nombre => {
        this.ficha3Form.get(nombre)?.valueChanges.subscribe(() => validarGrupo());
      });

      // Ejecutar la validacin inicial
      validarGrupo();
    }
  });
}

