private controlarRadioPorCheckbox(): void {
  const pares: [string, string][] = [
    ['p3_2_a', 'p3_2_a_region'],
    ['p3_2_b', 'p3_2_b_region'],
    ['p3_2_c', 'p3_2_c_region'],
    ['p3_2_d', 'p3_2_d_region'],
    ['p3_2_e', 'p3_2_e_region'],
    ['p3_2_f', 'p3_2_f_region'],
    ['p3_2_g', 'p3_2_g_region'],
    ['p3_2_h', 'p3_2_h_region'],
    ['p3_2_i', 'p3_2_i_region'],
    ['p3_2_j', 'p3_2_j_region'],
    ['p3_2_k', 'p3_2_k_region'],
    ['p3_2_l', 'p3_2_l_region'],
    ['p3_2_m', 'p3_2_m_region'],
    ['p3_2_n', 'p3_2_n_region'],
    ['p3_2_x', 'p3_2_x_region'],
    ['p3_2_o', 'p3_2_o_region'],
    ['p3_2_p', 'p3_2_p_region'],
    ['p3_2_q', 'p3_2_q_region'],
  ];

  pares.forEach(([checkbox, radio]) => {
    const checkControl = this.ficha3Form.get(checkbox);
    const radioControl = this.ficha3Form.get(radio);

    if (checkControl && radioControl) {
      // Estado inicial
      if (!checkControl.value) {
        radioControl.disable({ emitEvent: false });
      }

      // Escuchar cambios
      checkControl.valueChanges.subscribe((checked: boolean) => {
        if (checked) {
          radioControl.enable();
        } else {
          radioControl.reset();
          radioControl.disable();
        }
      });
    }
  });
}


