 //METODO QUE LIMPIA LA 1.1.1.2
 private limpiarPregunta1112(): void {
  const camposPregunta1112 = [
    'p1111FechaConvenio',
    'p1111Peridiocidad',
    'p1111FechaCaducidad',
    'p1112A', 'p1112B', 'p1112C', 'p1112D', 'p1112E', 'p1112F', 'p1112G', 'p1112H', 'p1112I',
    'p1112AA', 'p1112AB', 'p1112AC', 'p1112AD', 'p1112AE', 'p1112ADetalle',
    'p1112BA', 'p1112BB', 'p1112BC', 'p1112BD', 'p1112BE', 'p1112BDetalle',
    'p1112CA', 'p1112CB', 'p1112CC', 'p1112CD', 'p1112CE', 'p1112CDetalle',
    'p1112DA', 'p1112DB', 'p1112DC', 'p1112DD', 'p1112DE', 'p1112DDetalle',
    'p1112EA', 'p1112EB', 'p1112EC', 'p1112ED', 'p1112EE', 'p1112EDetalle',
    'p1112FA', 'p1112FB', 'p1112FC', 'p1112FD', 'p1112FE', 'p1112FDetalle',
    'p1112GA', 'p1112GB', 'p1112GC', 'p1112GD', 'p1112GE', 'p1112GDetalle',
    'p1112HA', 'p1112HB', 'p1112HC', 'p1112HD', 'p1112HE', 'p1112HDetalle',
    'p1112IO', 'p1_1_1_2_i_otro_detalle', 'p1_1_1_2_i_detalle'
  ];

  camposPregunta1112.forEach(campo => {
    const control = this.ficha1Form.get(campo);
    if (control && control.value) {
      control.reset(); // Limpia solo si tiene valor
    }
  });
}

