@Output() onEstadoActualizado = new EventEmitter<string>(); // NUEVO
  
  guardarSeccion1() {
  // Validar si el formulario es invlido
  if (this.ficha1Form.invalid) {
    this.ficha1Form.markAllAsTouched();
    return;
  }

  // Validar si la pregunta 1.4 es afirmativa
  if (this.ficha1Form.get('p1_4')?.value === 'S') {
    // Validar si al menos un checkbox de la pregunta 1.4.4 est marcado
    const checkboxes144 = [
      'p1_4_4_a',
      'p1_4_4_b',
      'p1_4_4_c',
      'p1_4_4_d',
      'p1_4_4_e'
    ];

    const algunoMarcado = checkboxes144.some(nombre => this.ficha1Form.get(nombre)?.value);

    if (!algunoMarcado) {
      // Mostrar mensaje de error si ninguno est marcado
      Swal.fire({
        icon: 'error',
        title: 'Error al guardar',
        text: 'Debe marcar al menos una opcin en la pregunta 1.4.4.',
        confirmButtonText: 'OK'
      });
      return;
    }
  }

  // Validar fechas del convenio
  this.validarFechasConvenio();

  // Transformar los valores del formulario
  const seccionData = this.transformarValoresCheckbox(this.ficha1Form);

    seccionData.estado_s1 = 'C'
  // Enviar los datos al backend
  this.http.post<any>('/api/ficha/guardarFichaSeccion1', seccionData).subscribe({
    next: (respuesta) => {
      console.log('Mensaje:', respuesta.mensaje);
      console.log('Estado:', respuesta.estado);

      localStorage.setItem('pantbc_s1', JSON.stringify(seccionData));
      this.onGuardar.emit();
    this.onEstadoActualizado.emit(seccionData.estado_s1); //  EMITE estado_s2

      //  Mensaje de xito
      Swal.fire({
        icon: 'success',
        title: respuesta.mensaje,
        text: `Estado: ${respuesta.estado}`,
        showConfirmButton: false,
        timer: 2500
      });
    },
    error: (err) => {
      console.error('Error al guardar seccin 1:', err);

      //  Mensaje de error
      Swal.fire({
        icon: 'error',
        title: 'Error al guardar',
        text: 'No se pudo guardar la Seccin 1',
        confirmButtonText: 'OK'
      });
    }
  });
}


 
  guardarSeccion1Incompleta() {
  const seccionData = this.transformarValoresCheckbox(this.ficha1Form); 

  seccionData.estado_s1 = 'I'; //  Siempre guardar como INCOMPLETO

  this.http.post<any>('/api/ficha/guardarFichaSeccion1', seccionData).subscribe({
    next: (respuesta) => {
      console.log('Mensaje:', respuesta.mensaje);
      console.log('Estado:', respuesta.estado);

      localStorage.setItem('pantbc_s1', JSON.stringify(seccionData));
      this.onGuardar.emit();
      this.onEstadoActualizado.emit('I'); // Emitimos 'I'

      Swal.fire({
        icon: 'info',
        title: 'Seccion 1 Guardada Parcialmente',
        timer: 3000,
        showConfirmButton: false
      });
    },
    error: (err) => {
      console.error('Error al guardar seccin 2 como incompleta:', err);
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: 'No se pudo guardar como incompleto',
      });
    }
  });
}
 
