  onOtroCheckboxChange(checkboxControlName: string, detalleControlName: string): void {
    const checkboxControl = this.ficha5Form.get(checkboxControlName);
    const detalleControl = this.ficha5Form.get(detalleControlName);
    if (checkboxControl?.value === false) {
      detalleControl?.reset();
      detalleControl?.disable(); 
    } else {
      detalleControl?.enable(); 
    }
  }

  private transformarValoresCheckbox(formData: any): any {
    const transformValue = (key: string, value: any): any => {
      // Si es booleano y no es un campo *_region, transforma a 'S' / ''
      if (typeof value === 'boolean' && !key.endsWith('_region')) {
        return value ? 'S' : '';
      } else if (value === null || value === undefined) {
        return '';
      } else if (Array.isArray(value)) {
        return value.map(v => transformValue(key, v));
      } else if (typeof value === 'object' && value !== null) {
        return transformObject(value);
      }
      return value;
    };

    const transformObject = (obj: any): any => {
      const result: any = {};
      for (const key in obj) {
        if (obj.hasOwnProperty(key)) {
          result[key] = transformValue(key, obj[key]);
        }
      }
      return result;
    };

    return transformObject(formData);
  }

  validarLongitudMaxima(controlName: string, maxLength: number): void {
    const control = this.ficha5Form.get(controlName);
    if (control) {
      const valor = control.value;
      if (valor && valor.length > maxLength) {
        control.setValue(valor.slice(0, maxLength));
      }
    }
  }

  private controlarCampoDetalle(radioControlName: string, detalleControlName: string, habilitarValor: string): void {
    const radioControl = this.ficha5Form.get(radioControlName);
    const detalleControl = this.ficha5Form.get(detalleControlName);

    if (!radioControl || !detalleControl) return;

    radioControl.valueChanges.subscribe(value => {
      if (value === habilitarValor) {
        detalleControl.setValidators([Validators.required]);
        detalleControl.enable();
      } else {
        detalleControl.clearValidators();
        detalleControl.reset();
        detalleControl.disable();
      }
      detalleControl.updateValueAndValidity();
    });

    //  Evala el valor inicial si ya viene cargado
    if (radioControl.value === habilitarValor) {
      detalleControl.setValidators([Validators.required]);
      detalleControl.enable();
    } else {
      detalleControl.clearValidators();
      detalleControl.disable();
    }
    detalleControl.updateValueAndValidity();
  }

  private validarGrupoCheckboxes(checkboxes: string[]): void {
    const validarGrupo = () => {
      const algunoSeleccionado = checkboxes.some(nombre => this.ficha5Form.get(nombre)?.value);
      const error = algunoSeleccionado ? null : { required: true };
      checkboxes.forEach(nombre => {
        this.ficha5Form.get(nombre)?.setErrors(error);
      });
    };

    checkboxes.forEach(nombre => {
      this.ficha5Form.get(nombre)?.valueChanges.subscribe(() => validarGrupo());
    });

    validarGrupo();
  }

  // Verificar si todos los campos requeridos estn completos
  isFormValid(): boolean {
    if (!this.ficha5Form) return false;

    // Verificamos los campos obligatorios principales
    const requiredFields = [
      'p5_1', 'p5_2', 'p5_3', 'p5_4'
    ];

    // Verificar campos condicionales
    if (this.ficha5Form.get('p5_1')?.value === 'E' && !this.ficha5Form.get('p5_1_detalle')?.value) {
      return false;
    }

    // Verificar los campos principales requeridos
    for (const field of requiredFields) {
      if (!this.ficha5Form.get(field)?.value) {
        return false;
      }
    }

    return true;
  }


