//////Funcion que se usa cuando una pregunta depende de otra SOLO PARA RADIOS, SELECT O TIPOS TEXT ( la vuelve requerida y la limpia ) ///////////////////
private configurarValidacionCondicional(
  controlPrincipal: string,
  camposDependientes: string[],
  valorHabilitador: string
): void {
  const control = this.ficha5Form.get(controlPrincipal);

  control?.valueChanges.subscribe(valor => {
    camposDependientes.forEach(nombre => {
      const campo = this.ficha5Form.get(nombre);
      if (valor === valorHabilitador) {
        campo?.setValidators([Validators.required]);
      } else {
        campo?.clearValidators();
        campo?.reset();
      }
      campo?.updateValueAndValidity();
    });
  });
  if (control?.value !== undefined) {
    camposDependientes.forEach(nombre => {
      const campo = this.ficha5Form.get(nombre);
      if (control.value === valorHabilitador) {
        campo?.setValidators([Validators.required]);
      } else {
        campo?.clearValidators();
        campo?.reset();
      }
      campo?.updateValueAndValidity();
    });
  }
}

private limpiarDetalleSiCheckboxDesmarcado(
  checkboxControlName: string,
  detalleControlName: string
): void {
  const checkbox = this.ficha5Form.get(checkboxControlName);
  const detalle = this.ficha5Form.get(detalleControlName);

  if (!checkbox || !detalle) return;

  //  Suscripcin: reacciona a cambios en el checkbox
  checkbox.valueChanges.subscribe(valor => {
    if (valor === true) {
      detalle.enable();
      detalle.setValidators([Validators.required]);
    } else {
      detalle.setValue('');
      detalle.reset();
      //detalle.disable();
      detalle.clearValidators();
      detalle.setErrors(null);
    }
    detalle.updateValueAndValidity();
  });

  //  Evaluacin inicial inteligente
  const detalleTieneValor = !!detalle.value && detalle.value.trim() !== '';

  if (checkbox.value === true || detalleTieneValor) {
    detalle.enable();
    detalle.setValidators([Validators.required]);
  } else {
    detalle.setValue('');
    detalle.reset();
    //detalle.disable();
    detalle.clearValidators();
    detalle.setErrors(null);
  }

  detalle.updateValueAndValidity();
}



