private controlarGrupoCheckboxesPorRadio(
  controlRadioName: string,
  checkboxNames: string[],
  valorHabilitador: string,
  detalleCheckboxName?: string
): void {
  const controlRadio = this.ficha5Form.get(controlRadioName);
  if (!controlRadio) return;

  const limpiarGrupo = () => {
    checkboxNames.forEach(nombre => {
      const control = this.ficha5Form.get(nombre);
      control?.reset();
      control?.clearValidators();
      control?.setErrors(null);
      control?.updateValueAndValidity();
    });

    if (detalleCheckboxName) {
      const detalleControl = this.ficha5Form.get(detalleCheckboxName);
      if (detalleControl) {
        detalleControl.setValue('');
        detalleControl.reset();
        detalleControl.clearValidators();
        detalleControl.setErrors(null);
        detalleControl.updateValueAndValidity();
      }
    }
  };

  const validarGrupo = () => {
    const algunoSeleccionado = checkboxNames.some(nombre => this.ficha5Form.get(nombre)?.value);
    const error = algunoSeleccionado ? null : { required: true };
    checkboxNames.forEach(nombre => {
      const checkbox = this.ficha5Form.get(nombre);
      if (checkbox) checkbox.setErrors(error);
    });
  };

  // Suscripcin al cambio del radio
  controlRadio.valueChanges.subscribe(valor => {
    if (valor === valorHabilitador) {
      validarGrupo();
      checkboxNames.forEach(nombre => {
        this.ficha5Form.get(nombre)?.valueChanges.subscribe(() => validarGrupo());
      });
    } else {
      limpiarGrupo();
    }
  });

  // Al cargar, evaluamos el valor actual
  if (controlRadio.value === valorHabilitador) {
    validarGrupo();
  } else {
    limpiarGrupo();
  }
}





private controlarCampoCondicionalConRadioYDetalleSimplificado(
  campoCondicionalName: string,
  campoRadioName: string,
  campoDetalleName: string
): void {
  const campoCondicional = this.ficha5Form.get(campoCondicionalName);
  const campoRadio = this.ficha5Form.get(campoRadioName);
  const campoDetalle = this.ficha5Form.get(campoDetalleName);

  if (!campoCondicional || !campoRadio || !campoDetalle) return;

  // Escucha cambios del checkbox o campo condicional
  campoCondicional.valueChanges.subscribe(valor => {
    if (valor) {
      campoRadio.setValidators([Validators.required]);
      campoRadio.enable();
      campoRadio.updateValueAndValidity();
    } else {
      // Si se desmarca, limpiar y desactivar todo
      campoRadio.reset();
      campoRadio.clearValidators();
      campoRadio.disable();
      campoRadio.updateValueAndValidity();

      campoDetalle.enable();              // necesario para limpiar correctamente
      campoDetalle.setValue('');          // borra manualmente
      campoDetalle.reset();               // limpia estado interno
      //campoDetalle.disable();             // lo desactiva
      campoDetalle.clearValidators();     
      campoDetalle.setErrors(null);
      campoDetalle.updateValueAndValidity();
    }
  });

  // Evaluacin inicial
  if (!campoCondicional.value) {
    campoRadio.reset();
    campoRadio.clearValidators();
    campoRadio.disable();
    campoRadio.updateValueAndValidity();

    campoDetalle.enable();
    campoDetalle.setValue('');
    campoDetalle.reset();
    campoDetalle.disable();
    campoDetalle.clearValidators();
    campoDetalle.setErrors(null);
    campoDetalle.updateValueAndValidity();
  } else {
    campoRadio.setValidators([Validators.required]);
    campoRadio.enable();
    campoRadio.updateValueAndValidity();
  }
}



///////NO INCLUYE CAMPO DETALLE, CUANDO EL RADIO ,  DEPENDE DEL MARCADO DE UNA OPCION DE UN GRUPO DE CHECKBOX////

private controlarCampoCondicionalConRadioSimplificado(
  campoCondicionalName: string,
  campoRadioName: string
): void {
  const campoCondicional = this.ficha5Form.get(campoCondicionalName);
  const campoRadio = this.ficha5Form.get(campoRadioName);

  if (!campoCondicional || !campoRadio) return;

  // Escucha cambios del checkbox o campo condicional
  campoCondicional.valueChanges.subscribe(valor => {
    if (valor) {
      campoRadio.setValidators([Validators.required]);
      campoRadio.enable();
    } else {
      campoRadio.reset();
      campoRadio.clearValidators();
      campoRadio.disable();
    }
    campoRadio.updateValueAndValidity();
  });

  // Evaluacin inicial
  if (!campoCondicional.value) {
    campoRadio.reset();
    campoRadio.clearValidators();
    campoRadio.disable();
  } else {
    campoRadio.setValidators([Validators.required]);
    campoRadio.enable();
  }
  campoRadio.updateValueAndValidity();
}

