private configurarPregunta510(): void {
  const pregunta510 = this.ficha5Form.get('p5_10');
  const ene = this.ficha5Form.get('p5_10_1_ene');
  const feb = this.ficha5Form.get('p5_10_1_feb');
  const mar = this.ficha5Form.get('p5_10_1_mar');

  if (!pregunta510 || !ene || !feb || !mar) return;

  // Suscripcin reactiva a cambios en p5_10
  pregunta510.valueChanges.subscribe((valor: string) => {
    const controles = [ene, feb, mar];

    if (valor === 'S') {
      controles.forEach(control => {
        control.setValidators([Validators.required]);
        control.updateValueAndValidity();
      });
    } else {
      controles.forEach(control => {
        control.reset();
        control.clearValidators();
        control.setErrors(null);
        control.updateValueAndValidity();
      });
    }
  });

  // Evaluacin inicial (por si ya vino seteado al editar)
  if (pregunta510.value === 'S') {
    [ene, feb, mar].forEach(c => {
      c.setValidators([Validators.required]);
      c.updateValueAndValidity();
    });
  }
}


     
