@Output() onEstadoActualizado = new EventEmitter<string>(); // NUEVO

  guardarSeccion5() {
    this.formSubmitted = true;
    
    if (!this.ficha5Form.valid) {
      // Mostrar error si el formulario no es vlido
      Swal.fire({
        icon: 'error',
        title: 'Formulario incompleto',
        text: 'Por favor complete todos los campos requeridos.',
        confirmButtonText: 'OK'
      });
      return;
    }
    
    this.guardarConPost();
  }

  private guardarConPost(): void {
    let seccionData = this.ficha5Form.getRawValue();

    // Transformar booleanos a 'S' o ''
    seccionData = this.transformarValoresCheckbox(seccionData);

       seccionData.estado_s5 = 'C'
    // Enviar al backend (CORREGIDO: cambiado de seccion4 a seccion5)
    this.http.post<any>('/api/ficha/guardarFichaSeccion5', seccionData).subscribe({
      next: (respuesta) => {
        console.log('Mensaje:', respuesta.mensaje);
        console.log('Estado:', respuesta.estado);

        localStorage.setItem('pantbc_s5', JSON.stringify(seccionData));
        this.onGuardar.emit();
    this.onEstadoActualizado.emit(seccionData.estado_s5); //  EMITE estado_s2

        Swal.fire({
          icon: 'success',
          title: respuesta.mensaje,
          text: `Estado: ${respuesta.estado}`,
          showConfirmButton: false,
          timer: 2500
        });
      },
      error: (err) => {
        console.error('Error al guardar seccin 5:', err);
        Swal.fire({
          icon: 'error',
          title: 'Error al guardar',
          text: 'No se pudo guardar la Seccin 5',
          confirmButtonText: 'OK'
        });
      }
    });
  }


  
    guardarSeccion5Incompleta() {
    const seccionData = this.transformarValoresCheckbox(this.ficha5Form.value);
    seccionData.estado_s5 = 'I'; //  Siempre guardar como INCOMPLETO
  
        this.verificarCamposInvalidos();
    this.http.post<any>('/api/ficha/guardarFichaSeccion5', seccionData).subscribe({
      next: (respuesta) => {
        console.log('Mensaje:', respuesta.mensaje);
        console.log('Estado:', respuesta.estado);
  
        localStorage.setItem('pantbc_s5', JSON.stringify(seccionData));
        this.onGuardar.emit();
        this.onEstadoActualizado.emit('I'); // Emitimos 'I'
  
        Swal.fire({
          icon: 'info',
          title: 'Guardado Parcialmente',
          text: 'La Seccin 5 fue guardada de forma PARCIAL',
          timer: 2500,
          showConfirmButton: false
        });
      },
      error: (err) => {
        console.error('Error al guardar seccin 2 como incompleta:', err);
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: 'No se pudo guardar como incompleto',
        });
      }
    });
  }
