actualizarEstadoSeccion(seccion: SeccionKey, estado: string) {
  this.completadas[seccion] = (estado === 'C');
  this.estadoSecciones[seccion] = estado; //  ACTUALIZA tambin el estado

  localStorage.setItem('pantbc_completadas', JSON.stringify(this.completadas));

  //  Re-evala si ya est toda la ficha completa
  this.evaluarEstadoFichaCompleta();
}


todasSeccionesGuardadas(): boolean {
  for (let i = 1; i <= 7; i++) {
    const datos = localStorage.getItem(`pantbc_s${i}`);
    
    // Validacin para evitar el error
    if (!datos) return false;

    try {
      const parsed = JSON.parse(datos);

      if (!parsed || typeof parsed !== 'object') return false;

      const valores = Object.values(parsed);
      if (valores.some(v => v === null || v === '')) return false;
    } catch (e) {
      console.error(` Error parseando seccin ${i}:`, e);
      return false;
    }
  }
  return true;
}


  mostrarSeccion(numero: number) {
    const key = `s${numero}` as keyof typeof this.completadas;
    const datosSeccion = localStorage.getItem(`pantbc_s${numero}`);
  
    if (datosSeccion) {
      const campos = JSON.parse(datosSeccion);
      const todosCompletos = Object.values(campos).every(valor => valor !== null && valor !== '');
      this.completadas[key] = campos.estado_s1 === 'C'; // usa estado_s2 real
      this.completadas[key] = campos.estado_s2 === 'C'; // usa estado_s2 real
      this.completadas[key] = campos.estado_s3 === 'C'; // usa estado_s2 real
      this.completadas[key] = campos.estado_s4 === 'C'; // usa estado_s2 real
      this.completadas[key] = campos.estado_s5 === 'C'; // usa estado_s2 real
      this.completadas[key] = campos.estado_s6 === 'C'; // usa estado_s2 real
      this.completadas[key] = campos.estado_s7 === 'C'; // usa estado_s2 real

      //this.completadas[key] = todosCompletos;

      if (campos[`estado_${key}`] === 'C') {
          this.completadas[key] = true;
          this.estadoSecciones[key] = 'C'; // tambin actualiza el estado real
        } else {
          this.completadas[key] = false;
          this.estadoSecciones[key] = 'I';
        }

      localStorage.setItem('pantbc_completadas', JSON.stringify(this.completadas));
    } else {
      this.completadas[key] = false;
      localStorage.setItem('pantbc_completadas', JSON.stringify(this.completadas));
      this.snackBar.open(`Seccin ${numero} an no ha sido guardada.`, 'Cerrar', {
        duration: 3000,
        panelClass: ['snackbar-warning'],
        horizontalPosition: 'right',
        verticalPosition: 'top'
      });
    }
  }
  

