  ngOnInit(): void {

    //Obtener datos del entrevistador al iniciar
    const token = localStorage.getItem('token');
    const headers = token ? new HttpHeaders({ Authorization: `Bearer ${token}` }) : undefined;

      this.fichaForm.get('nomFuncioPca')?.valueChanges.subscribe((valor: string | null) => {
      if (valor && valor !== valor.toUpperCase()) {
        this.fichaForm.get('nomFuncioPca')?.setValue(valor.toUpperCase(), { emitEvent: false });
      }
    });


      this.http.get('/api/ficha/datosEntrevistador', { headers }).subscribe({
        next: (data: any) => {
          this.fichaForm.patchValue({
            txt_desc_depe_tde: data.txt_desc_depe_tde,
            txt_comisionado: data.txt_comisionado
          });
          console.log(' Datos del entrevistador cargados automticamente:', data);
        },
        error: (err) => {
          console.error(' Error al obtener datos del entrevistador:', err);
        }
      });

    // Recupera y aplica los datos del formulario principal
    const datosGuardados = localStorage.getItem('fichaPantbc');
    if (datosGuardados) {
      const fichaParsed = JSON.parse(datosGuardados);
      this.fichaForm.patchValue(fichaParsed);
  
      // Restaura el estado de la ficha desde los datos guardados
      if (fichaParsed.estado) {
        this.mensajeEstadoFicha = fichaParsed.estado;
      }
    }
  
    // Recupera el ID de la ficha guardada
    const idGuardado = localStorage.getItem('idFichaPantbc');
                if (idGuardado) {
                  this.idFichaGuardado = +idGuardado;

                  // Limpia antes de llamar al backend
                  this.fichaForm.reset();
                  this.mostrar = { s1: false, s2: false, s3: false, s4: false, s5: false, s6: false, s7: false };
                  this.completadas = { s1: false, s2: false, s3: false, s4: false, s5: false, s6: false, s7: false };

                  for (let i = 1; i <= 7; i++) {
                    localStorage.removeItem(`pantbc_s${i}`);
                  }
                  localStorage.removeItem('pantbc_completadas');
                  localStorage.removeItem('fichaPantbc');

                  this.cargarFichaCompleta(this.idFichaGuardado);
                }

  
    // Restaura qu secciones se estn mostrando
    const seccionesGuardadas = localStorage.getItem('pantbc_secciones');
    if (seccionesGuardadas) {
      this.mostrar = JSON.parse(seccionesGuardadas);
    }
  
    // Restaura el estado de completado de cada seccin
    // const completadasGuardadas = localStorage.getItem('pantbc_completadas');
    // if (completadasGuardadas) {
    //   this.completadas = JSON.parse(completadasGuardadas);
    // }

    const completadasGuardadas = localStorage.getItem('pantbc_completadas');
    if (completadasGuardadas) {
      this.completadas = JSON.parse(completadasGuardadas);
    } else {
      this.completadas = { s1: false, s2: false, s3: false, s4: false, s5: false, s6: false, s7: false };
    }


    this.fichaForm.get('nivelPca')?.valueChanges.subscribe((valor) => {
      const detalleControl = this.fichaForm.get('nivelDetallePca');
    
      if (valor === 'E') {
        detalleControl?.setValidators([Validators.required, Validators.maxLength(498)]);
      } else {
        detalleControl?.clearValidators();
        detalleControl?.setValue('');
      }
    
      detalleControl?.updateValueAndValidity();
    });

    
    
    this.fichaForm.get('nivelPantbc')?.valueChanges.subscribe((valor) => {
      const detalleControl = this.fichaForm.get('nivelDetallePantbc');
    
      if (valor === 'E') {
        detalleControl?.setValidators([Validators.required, Validators.maxLength(498)]);
      } else {
        detalleControl?.clearValidators();
        detalleControl?.setValue('');
      }
    
      detalleControl?.updateValueAndValidity();
    });

    this.fichaForm.get('timeFuncioPca')?.valueChanges.subscribe((valor) => {
      const detalleControl = this.fichaForm.get('timeFuncioDetallePca');
    
      if (valor === 'F') {
        detalleControl?.setValidators([Validators.required, Validators.maxLength(498)]);
      } else {
        detalleControl?.clearValidators();
        detalleControl?.setValue('');
      }
    
      detalleControl?.updateValueAndValidity();
    }); 

//////////////
    this.fichaForm.get('timeFuncioPantbc')?.valueChanges.subscribe((valor) => {
      const detalleControl = this.fichaForm.get('timeFuncioDetallePantbc');
    
      if (valor === 'F') {
        detalleControl?.setValidators([Validators.required, Validators.maxLength(498)]);
      } else {
        detalleControl?.clearValidators();
        detalleControl?.setValue('');
      }
    
      detalleControl?.updateValueAndValidity();
    });

////////carga el padrn en select////////

    this.fichaService.obtenerPadron().subscribe({
      next: (data) => {
        this.padronList = data.sort((a, b) => {
          const getClave = (nombre: string) => {
            const palabras = nombre.split(' ');
            return palabras.length >= 4 ? palabras.slice(3).join(' ') : palabras.join(' ');
          };
    
          const claveA = getClave(a.nom_entidad).toLowerCase();
          const claveB = getClave(b.nom_entidad).toLowerCase();
          return claveA.localeCompare(claveB);
        });
      },
      error: (err) => console.error('Error al obtener padrn:', err)
    });
    
///////////////////////
    const usuario = this.obtenerUsuarioDelToken(); 
  // Setear el usuario tcnico para guardar (usuRegistro)
  this.fichaForm.patchValue({ usuRegistro: usuario });


  }

  obtenerUsuarioDelToken(): string {
    const payload = JSON.parse(atob(localStorage.getItem('token')!.split('.')[1]));
    return payload.sub; // o el claim que tenga el username
  }

///////////////////////////////////////////////////////////////////



onEntidadSelected(nombreSeleccionado: string): void {
  const seleccionado = this.padronList.find(p => p.nom_entidad === nombreSeleccionado);

  if (seleccionado) {
    // Rellenar campos del formulario
    this.fichaForm.patchValue({
      nomEntidad: seleccionado.nom_entidad,
      codUnico: seleccionado.ruc,
      departamento: seleccionado.des_departament,
      provincia: seleccionado.des_provincia,
      distrito: seleccionado.des_distrito,
      correoInstitucional: seleccionado.correo,
      telInstitucional: seleccionado.telefono
    });

    // Validar si la entidad ya existe en la tabla
    this.fichaService.verificarEntidadExistente(seleccionado.nom_entidad).subscribe({
      next: (existe) => {
        const control = this.fichaForm.get('nomEntidad');
        if (existe) {
          control?.setErrors({ entidadDuplicada: true });
        } else {
          control?.setErrors(null);
        }
      },
      error: (err) => {
        console.error(' Error al verificar entidad existente:', err);
      }
    });
  }
}


