////////////////////FUNCTIONES VALIDACIN ////////////////
permitirSoloNumeros(event: KeyboardEvent): void {
  const tecla = event.key;
  const teclasPermitidas = ['Backspace', 'ArrowLeft', 'ArrowRight', 'Tab', 'Delete'];

  if (!/^[0-9]$/.test(tecla) && !teclasPermitidas.includes(tecla)) {
    event.preventDefault();
  }
}


static fechaFinMayorIgualInicioValidator(): ValidatorFn {
  return (formGroup: AbstractControl): ValidationErrors | null => {
    const inicio = formGroup.get('fchSupervision')?.value;
    const fin = formGroup.get('fchSupervisionFin')?.value;

    if (inicio && fin && new Date(fin) < new Date(inicio)) {
      return { fechaInvalida: true };
    }
    return null;
  };
}

